<template>
  <div class="tab-bar-item" @click="btnClick">
    <!-- <img src="../../assets/img/tab-bar/home.svg" alt="">
    <div>首页</div> -->
    <div v-if="!isActive"><slot name="item-icon"></slot></div>
    <div v-else><slot name="item-icon-active"></slot></div>
    <div :style="activeStyle"><slot name="item-text"></slot></div>
  </div>
</template>
<script>
  export default {
    name : 'tab-bar-item',
    props : {
      path : String,
      actColor : {
        type : String,
        default : 'red',
      }
    },
    data() {
      return {
        
      }
    },
    computed: {
      isActive() {
        // return this.$route.path.indexOf(this.path) !== -1;
        return this.$route.path.includes(this.path);
      },
      activeStyle() {
        return this.isActive ? {color : this.actColor} : {}
      }
    },
    methods: {
      btnClick() {
        this.$router.push(this.path)
      },
    },
  }
</script>

<style scoped>

  .tab-bar-item {
    flex : 1;
    text-align: center;
    height: 49px;
    cursor: pointer;

    font-size: 14px;
  }

  .tab-bar-item img {
    width: 24px;
    height: 24px;
    margin-top: 3px;
    vertical-align: middle;
    margin-bottom: 2px;
  }
</style>